igns label and input vertically */\r\n  margin-bottom: 15px; /* Space between form groups */\r\n}\r\n\r\ninput:focus,\r\ntextarea:focus {\r\n  border: 3px solid rgb(13, 110, 253, 0.5);\r\n  outline: none;\r\n}\r\n\r\ninput[type=\"file\"] {\r\n  padding: 0; /* Adjust padding for file input */\r\n}\r\n\r\ninput[type=\"number\"] {\r\n  width: 50%; /* Adjust width for number input */\r\n}\r\n\r\nlabel {\r\n  font-weight: bold; /* Makes the label text bold */\r\n  margin-bottom: 5px; /* Space between label and input */\r\n  align-self: center;\r\n}\r\n\r\ninput[type=\"text\"],\r\ninput[type=\"number\"],\r\ninput[type=\"file\"],\r\ntextarea:focus {\r\n  padding: 10px;\r\n  font-size: 1.2em;\r\n}\r\n\r\n#restRating::placeholder {\r\n  font-size: 0.8em !important;\r\n}\r\n\r\n.restContent {\r\n  width: 500px;\r\n}\r\n\r\n.rest-button-group {\r\n  margin-top: 2em;\r\n  display: flex;\r\n}\r\n\r\n#saveRest,\r\n#cancelRest {\r\n  padding: 1em;\r\n  width: 30%;\r\n}\r\n\r\n#addRestaurant,\r\n#submitBtn {\r\n  display: block; /* Make the button a block element */\r\n  margin: 0 auto; /* Centers the button */\r\n  margin-bottom: 1em;\r\n  padding: 1.5em;\r\n  border: none;\r\n  background-color: #1677ff;\r\n  color: rgb(255, 255, 255);\r\n  border-radius: 8px;\r\n  -webkit-border-radius: 8px;\r\n  -moz-border-radius: 8px;\r\n  -ms-border-radius: 8px;\r\n  -o-border-radius: 8px;\r\n}\r\n\r\n#submitBtn {\r\n  margin-left: 0; /* Align button to the left */\r\n}\r\n\r\n.favContainer {\r\n  width: 100%;\r\n  margin: 0 auto;\r\n  display: grid;\r\n  grid-template-columns: repeat(\r\n    auto-fit,\r\n    minmax(300px, 1fr)\r\n  ); /* Adjusts number of columns */\r\n  max-width: 1200px; /* Set a max width so that 3 columns fit in */\r\n  gap: 1.5em;\r\n}\r\n\r\n.favContent {\r\n  margin: 0 auto;\r\n  width: 90%;\r\n  padding: 1em;\r\n  border: 1px solid rgba(0, 0, 0, 0.1);\r\n  border-radius: 10px;\r\n  -webkit-border-radius: 10px;\r\n}\r\n\r\n/* restDetails site */\r\n\r\n#mainContent {\r\n  padding: 1.5em;\r\n}\r\n.detail-content {\r\n  display: grid;\r\n  padding: 1em;\r\n  grid-template-columns: repeat(3, 1fr); /* Create 3 equal columns */\r\n  grid-template-areas:\r\n    \"item1 item1 item1\" /* Span item1 across 2 columns */\r\n    \"item2 item3 item3\"\r\n    \"item4 item4 item4\" /* Span item4 across 2 columns */\r\n    \"item5 item5 item5\"; /* Span item5 across 2 columns */\r\n  gap: 1em; /* Optional: Adds space between grid items */\r\n}\r\n\r\n.item1 {\r\n  grid-area: item1;\r\n  justify-content: flex-start;\r\n  column-gap: 3em;\r\n  align-items: center;\r\n}\r\n.item2 {\r\n  grid-area: item2;\r\n}\r\n.item3 {\r\n  grid-area: item3;\r\n}\r\n.item4 {\r\n  grid-area: item4;\r\n}\r\n.item5 {\r\n  grid-area: item5;\r\n}\r\n\r\n.item1,\r\n.item2,\r\n.item3,\r\n.item4,\r\n.item5 {\r\n  padding: 1.3em;\r\n  border-radius: 8px;\r\n  border: 1px solid rgba(0, 0, 0, 0.5);\r\n  -webkit-border-radius: 8px;\r\n  -moz-border-radius: 8px;\r\n  -ms-border-radius: 8px;\r\n  -o-border-radius: 8px;\r\n}\r\n\r\n.item2 img {\r\n  width: 100%;\r\n  height: auto; /* maintain aspect ratio */\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  display: block;\r\n  margin-bottom: 1em;\r\n  /* image */\r\n}\r\n\r\n.menuContent {\r\n  display: flex;\r\n  flex-direction: row;\r\n}\r\n\r\n.foods,\r\n.drinks {\r\n  width: 100%;\r\n}\r\n\r\n.restFoods,\r\n.restDrinks {\r\n  line-height: 2em;\r\n}\r\n\r\n.restReviews {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 2em;\r\n}\r\n\r\n.reviewContent {\r\n  border: 1px solid rgb(13, 110, 253, 0.5);\r\n\r\n  border-radius: 8px;\r\n  -webkit-border-radius: 8px;\r\n  -moz-border-radius: 8px;\r\n  -ms-border-radius: 8px;\r\n  -o-border-radius: 8px;\r\n  padding: 0.5em;\r\n}\r\n\r\n.restReviewName {\r\n  font-weight: bold;\r\n}\r\n\r\n.restReviewReview {\r\n  padding: 1em;\r\n}\r\n\r\n.reviewPersonDetails {\r\n  display: flex;\r\n  flex-direction: row;\r\n  gap: 1.5em;\r\n}\r\n\r\n.unlikeBtn,\r\n.favBtn,\r\n#addR